import "./projectlist.css"
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

function ProjectList() {
    //  
    const [projects, setProjects] = useState([]);
    const [showNewProjectMenu, setShowNewProjectMenu] = useState(false);

    //     
    const toggleNewProjectMenu = () => {
        setShowNewProjectMenu(!showNewProjectMenu);
    };

    const [activeMenu, setActiveMenu] = useState(null);
    
    const handleClick = (menu) => {
        setActiveMenu(menu);
    };

    //  
    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('http://localhost:8090/api/projects');
                if (!response.ok) {
                    throw new Error('Failed to fetch data');
                }
                const data = await response.json();
                setProjects(data);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData(); 
    }, []); 

    return (
        <div>
            <div>
            <div>
                <div className={`MenuBar ${activeMenu === "" ? "active" : "inactive"}`} onClick={() => handleClick("")}></div>
                <div className={`MenuBar ${activeMenu === "" ? "active" : "inactive"}`} onClick={() => handleClick("")}></div>
                <div className="MenuBar"></div>

           
            {projects.map((project, index) => (
                <div key={index} className="Projectlist-Box">
                  <Link to={`${process.env.REACT_APP_BASENAME}/projectdetail/${project.id}`}>
                    <p className="Projectlist-Title">{project.name}</p>
                    <p className="Projectlist-Sub"> : {project.startDate} ~ {project.endDate}</p>
                    </Link>
                </div>
            ))}
             <p className="Plus-Button" onClick={toggleNewProjectMenu}>+</p>

            {showNewProjectMenu && (
            <div className="Plus-Menu">
            <a href="/new" className="Plus-Text">  </a>
            <p className="Plus-Text"> </p>
            </div>
            )}
</div>

        </div>
        </div>
    );
}

export default ProjectList;
